.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
@keyframes backgroundColorChange {
  0% { background-color: #d63814; }
  25% { background-color: #ff7f50; } /* Coral */
  50% { background-color: #20b2aa; } /* LightSeaGreen */
  75% { background-color: #1e90ff; } /* DodgerBlue */
  100% { background-color: #d63814; } /* Back to original */
}

.App-header {
  animation: backgroundColorChange 5s infinite;
  
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.butt{
  justify-content: left;
  margin-top: 20px;
}
.bttn {
  margin: 0 20px; /* Add horizontal space between buttons */
  padding: 10px 20px; /* Optional: Add padding for better button appearance */
  border-radius: 3rem;
  border: none;
  color: black;
  font-size: 16px;
  cursor: pointer;

}
#Pre{
background-color: #61dafb;
}
#Nex{
  background-color: rgb(89, 243, 89);
}
.ReactPlayer {
  width: 90vw; /* Width of the player relative to viewport width */
  height: 80vh; /* Height of the player relative to viewport height */
  max-width: 1200px; /* Optional: max width of the player */
  max-height: 800px; /* Optional: max height of the player */
}